<template>
  <v-app class="myFont">
    <Navbar/>
    <v-main>
      <Presentation :observer="observer"/>
      <Charts :observer="observer" :observed="sections['Charts']"/>
      <Features :observer="observer" :observed="sections['Features']"/>
      <Tools :observer="observer" :observed="sections['Tools']"/>
      <Suscription :observer="observer" :observed="sections['Suscription']"/>
      <Links :observer="observer" :observed="sections['Links']"/>
    </v-main>
  </v-app>
</template>

<script>
// import lazyLoadComponent from './utils/lazy-load'
import Navbar from './components/Navbar/Navbar.vue'
import Presentation from './components/Sections/Presentation.vue'
import Charts from './components/Sections/Charts.vue'
import Features from './components/Sections/Features.vue'
import Tools from './components/Sections/Tools.vue'
import Suscription from './components/Sections/Suscription.vue'
import Links from './components/Sections/Links.vue'

export default {
  name: 'App',

  components: {
    Navbar,
    Presentation,
    Charts,
    Features,
    Tools,
    Suscription,
    Links,
    // : lazyLoadComponent({
    //   componentFactory: () => import('./components/Sections/Links.vue'),
    //   loading: Empty,
    //   loadingFactory: () => import('./components/Sections/Empty.vue'),
    // }),
  },
  created() {
    this.observer = new IntersectionObserver(
      this.onElementObserved, 
      {
        root: this.$el,
      }
    );
  },
  methods: {
    onElementObserved(entries) {
      entries.forEach(({ target, intersectionRatio}) => {
        if (intersectionRatio <= 0) {
          setTimeout(() => {
            const i = target.getAttribute("name");
            if(i!="Presentation") this.sections[i] = false
          }, 100)
          return;
        }
      
        setTimeout(() => {
          const i = target.getAttribute("name");
          if(i!="Presentation") this.sections[i] =  true
        }, 100)
      });
    }
  },
  data: () => ({
    observer: null,
    sections: {
      Presentation: true,
      Charts: false,
      Features: false,
      Tools: false,
      Suscription: false,
      Links: false,
    }
  }),
};
</script>

