<template>
    <div name="Charts" class="principal" style="background-color: #f0f0f0">
        <v-row style="height: 100vh; transition: all .3s ease-out; overflow: hidden">
            <v-col style="margin-top: 100px;">
                <v-row no-gutters justify="center">
                    <v-col sm="10" class="bottom-top">
                        <div
                            class="text-h2 font-weight-bold text-center text1"
                            v-text="'All chart types available anywhere you code'"
                        />
                        
                        <div
                            class="text-h4 font-weight-bold text-center text2"
                            v-text="'The largest set of charts and graphs available in the market.'"
                        />
                        <p 
                            class="text-center text3"
                        >
                            Pick from our library of custom-crafted charts or create your own. Manage your visualizations directly within your project to ensure graphs and charts look great and stay on brand.
                        </p>
                        
                        <v-spacer style="height: 40px"/>

                        <v-row justify="center" no-gutters>

                            <v-btn
                                href="#"
                                x-large
                                color="secondary"
                                style="text-transform: none; margin-left: 5px"
                            >
                                <span>View all charts</span>
                                <v-icon
                                    dark
                                    right
                                >
                                    mdi-arrow-right
                                </v-icon>
                            </v-btn>
                        </v-row>
                    </v-col>
                </v-row>
                <div v-if="charts" :style="slideTransform">
                    <div class="chart" v-for="chart in charts" :key="chart.id">
                        <v-img
                            v-bind:src="require('../../assets/svg/' + chart.url)"
                            width="600"
                        />
                    </div>
                </div>
                <a href="#features" v-on:click="jump" class="arrow"/>
            </v-col>
        </v-row>
    </div>
</template>

<script>
export default {
    name: 'Charts',
    props: ['observer', 'observed'],
    created () {
        window.addEventListener('scroll', this.handleScroll);
    },
    mounted() {
        this.observer.observe(this.$el);
    },
    data: () => ({
        charts: [
            { id: 0, url: 'jcfx8-2D-normal-stack-area.svg' },
            { id: 1, url: 'jcfx8-Solid-Bg-Plot-Area.svg' },
            { id: 2, url: 'jcfx8-Lines-and-Stripes.svg' },
            { id: 3, url: 'jcfx8-Pie-Chart.svg' },
            { id: 4, url: 'jcfx8-Horizontal-Zero-Axis-Bar.svg' },
            { id: 5, url: 'jcfx8-3D-pyramid-.svg' },
            { id: 6, url: 'jcfx8-3D-Doughnut.svg' },
            { id: 7, url: 'jcfx8-2D-By-Side-Bars-with-shadow.svg' },
            { id: 8, url: 'jcfx8-Solid-Bg-Plot-Area.svg' },
            { id: 9, url: 'jcfx8-2D-normal-stack-area.svg' },
            { id: 10, url: 'jcfx8-Lines-and-Stripes.svg' },
            { id: 11, url: 'jcfx8-Pie-Chart.svg' },
            { id: 12, url: 'jcfx8-3D-pyramid-.svg' },
        ],
        transform: "translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)",
        slided: 0,
        lastScrollTop: 0,
        alreadySlidedDown: false,
        alreadySlidedUp: false,
        slidedDown: false,
        slidedUp: false,
    }),
    computed: {
        slideTransform (){
            return {
                transform: this.transform,
                "transform-style": "preserve-3d",
                display: "flex",
                direction: "row",
                "padding-top": "70px",
                "padding-bottom": "70px",
            };
        }
    },
    methods: {
        handleScroll() {
            var st = window.pageYOffset || document.documentElement.scrollTop; // Credits: "https://github.com/qeremy/so/blob/master/so.dom.js#L426"

            if(!this.alreadySlidedUp && !this.alreadySlidedDown){
                if (st > this.lastScrollTop && !this.slidedUp  && window.scrollY - 800 > 5){
                    console.log("ALLA", window.scrollY - 800)
                    this.slidedDown = true
                    window.scrollTo(0, 800);
                    const slided = this.slided + 60;
                    this.slided = slided
                    this.alreadySlidedDown = slided >= 3545
                    this.transform = "translate3d(-"+ slided + "px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)";
                    this.slidedDown = false;
                }
                else {
                    if(st < this.lastScrollTop && !this.slidedDown  && window.scrollY - 800 < -5) {
                        console.log("AQUI", window.scrollY - 800)
                        this.slidedUp = true
                        window.scrollTo(0, 800);
                        const slided = this.slided - 60;
                        this.slided = slided
                        this.alreadySlidedUp = slided < 0
                        this.transform = "translate3d(-"+ slided + "px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)";
                        this.slidedUp = false;
                    }
                }
            }
            else if (st < this.lastScrollTop && !this.slidedDown  && window.scrollY - 800 < 0 && this.slided>0) {
                this.alreadySlidedDown = false;
            }
            else if (st > this.lastScrollTop && !this.slidedUp  && window.scrollY - 800 > 0 && this.slided<3545) {
                this.alreadySlidedUp = false;
                if(this.slided<0) {
                    this.alreadySlidedUp = true;
                    this.slided = 0;
                }
            }
            
            this.lastScrollTop = st <= 0 ? 0 : st;
        },
        jump(){
            this.slided = 3600
            this.alreadySlidedDown = true;
            this.alreadySlidedUp = false;
            this.slidedDown= false;
            this.slidedUp= false;
            this.lastScrollTop = 1900
            this.transform = "translate3d(-"+ 3600 + "px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)";
        },
    }
}
</script>

<style scoped>
@keyframes bottom-top {
  0%   {
    transform: translate3d(0px, 50px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg);
    opacity: 0;
  }
  100% {
    transform: translate3d(0px, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg);
    opacity: 1;
  }
}
.bottom-top {
    animation-name: bottom-top;
    animation-duration: 1s;
    transform-style: preserve-3d;
}
.arrow {
    position: absolute;
    left: 0%;
    top: auto;
    right: 0%;
    bottom: 0%;
    width: 36px;
    height: 36px;
    margin-right: auto;
    margin-bottom: 15vh;
    margin-left: auto;
    background-image: url('../../assets/svg/arrow-down.svg');
    background-position: 50% 50%;
    background-size: contain;
    background-repeat: no-repeat;
    opacity: 0.29;
}
.arrow:hover {
  margin-bottom: 94px;
  opacity: 0.79;
}
.chart {
    margin: 25px;
    box-shadow: 0 0 60px 6px rgba(138, 148, 166, 0.35)
}
</style>