<template>
    <div name="Tools" >
        <v-row no-gutters v-if="observed" class="principal bg">
            <v-col cols="2" sm="6" style="overflow: hidden; padding: 0">
                <div
                    class="bg-img"
                    :style="bgImg"
                />
            </v-col>
            <v-col cols="10" sm="6" class="content">
                <v-row no-gutters class="section" align="center">
                    <div
                        style="padding-right: 110px"
                        :style="textTransform"
                        class="text-h3 font-weight-bold text1"
                        v-text="'A myriad of Business Intelligence and Data Visualization development tools available for instant download.'"
                    />
                </v-row>
                <v-row no-gutters v-for="tool in tools" :key="tool.id" class="section" align="center">
                    <v-col style="padding-right: 120px" :style="textTransform">
                        <v-img
                            v-bind:src="require('../../assets/svg/' + tool.url)"
                            max-width="70"
                            style="margin-bottom: 20px"
                        />

                        <div
                            class="text-h5 font-weight-bold text2"
                            v-text="tool.title"
                        />

                        <div
                            class="font-weight-thin text3"
                            v-text="tool.text"
                        />

                        <v-btn
                            href="#"
                            x-large
                            color="primary"
                            style="text-transform: none"
                        >
                            <span>{{'Buy ' + tool.title}}</span>
                            
                            <v-icon
                                color="white"
                                right
                            >
                                mdi-arrow-right
                            </v-icon>
                        </v-btn>
                    </v-col>
                </v-row>
            </v-col>
        </v-row>
        <div v-else style="min-height: 300vh;"/>
    </div>
</template>

<script>
export default {
    name: 'Tools',
    props: ['observer', 'observed'],
    created () {
        window.addEventListener('scroll', this.handleScroll);
    },
    mounted() {
        this.observer.observe(this.$el);
    },
    destroyed () {
        window.removeEventListener('scroll', this.handleScroll);
    },
    data: () => ({
        transform: 'translate3d(0vw, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)',
        transformText: 'translate3d(0px, 0px, 0px) scale3d(1.2, 1.2, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)',
        tools: [
            { id: 0, title: 'Chart FX 8', text: "The new generation of Chart FX 8 products are smarter than ever! With emphasis on data analytics, Chart FX 8 brings your web and desktop applications to a whole new level. Read more on this exciting new release from the leading data visualization and business intelligence tool for developers or download your trial today!", url: 'cfx8-logo-new.svg' },
            { id: 1, title: 'Chart FX Maps', text: "The new version of Chart FX Maps will be capable of displaying colors associated with data in your maps and allows geo-positioning of tags, additional visual layers (e.g. roads, bodies of water and landmarks) as well as a powerful rendering engine for greater versatility, improved performance and stunning aesthetics. While we release it please explore our new Maps Marketplace.", url: '016-globe.svg' },
            { id: 2, title: 'Chart FX Gauges', text: "Chart FX Gauges delivers a set of .NET components that allow developers to quickly and effectively integrate gauges into their client-server and web-based applications. With the addition of Gauges to the Chart FX family of products, developers can create digital dashboards with the productivity and consistency they've come to appreciate from other Software FX products.", url: '050-speedometer.svg' },
            { id: 3, title: 'Chart FX Extensions Pack', text: "With the Chart FX Extensions Pack developers are no longer required to spend hours configuring a generic charting component to specific data or behavior. Instead, Chart FX Extensions, which are .NET controls, can be added to either Chart FX for .NET 6.2 or Chart FX 7 to adapt to these data sources enabling organizations to quickly and economically develop solutions that meet unique business needs.", url: '011-global-marketing.svg' },
        ],
    }),
    computed: {
        bgImg() {
            return {
                "background-size": "contain",
                height: "100%",
                width: "300vw",
                opacity: "0.38",
                transform: this.transform,
                "background-position": "0px 0px",
                position: "static",
                "transform-style": "preserve-3d",
            };
        },
        textTransform (){
            return {
                transform: this.transformText,
                "transform-style": "preserve-3d",
            };
        }
    },
    methods: {
        handleScroll() {
            const scrollTop = (window.scrollY - 1935)/23 * -1;
            //start 1935
            this.transform = "translate3d("+ scrollTop + "vw, 0px, 0px) scale3d(1, 1, 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)"
            if(window.scrollY - 2585 > 0 && window.scrollY - 3275 < 0) {
                const cal = 1.1 - (window.scrollY - 2585)/690 * 0.1;
                this.transformText = "translate3d(0px, 0px, 0px) scale3d("+ cal + ", " + cal + ", 1) rotateX(0deg) rotateY(0deg) rotateZ(0deg) skew(0deg)";
            }
        } 
    }
}
</script>

<style scoped>
.bg {
    height: auto;
    background-color: #5f21e5;
    background-image: -webkit-gradient(linear, left top, left bottom, from(#5f21e5), to(#00c284))
}
.bg-img {
    background-image: url('../../assets/img/dashboard-sample-2.jpg');
    background-repeat: repeat-y;
}
.content {
    background-color: white;
    padding-top: 120px;
    padding-bottom: 120px
}
.section {
    min-height: 652px;
    padding-left: 70px;
}
</style>